import React from 'react'
import { Link } from 'react-router-dom'


const ItemLista = props => {
    return (
        <div className='card'>
            <img src={props.foto} className="imgtop" />
            <div className='card-body'>
                <h4 className='card-title'>{props.nome}</h4> 
                <p></p>
                <h6 className='card-title'>{props.marca}</h6>
                

                <h4 className='preco'>
                    {Number(props.preco).toLocaleString('pt-br', { style: 'currency', currency: 'BRL' })}
                </h4>

            </div>
            <button className="btn btn-primary mb-2" onClick={function () { alert('Adicionado ao seu carrinho!'); }}>Carrinho</button>

            <p></p>
            <button className="btn btn-success mb-2" onClick={function () { alert('Comprado com sucesso!'); }}>Comprar</button>

           

                
            

            <p></p>

        </div>

    )
}

export default ItemLista