import React, { Component } from 'react'

import Conecta from './Conecta'
import ItemLista from './ItemLista'


import './Listagem.css'

export default class Listagem extends Component {


    state = {
        ofertas: []
    }
    componentDidMount() {
        this.loadOfertas()
      }
    
    async componentDidMount() {
        if (navigator.onLine) {
            const lista = await Conecta.get('45966615-0b85-4fd3-9487-57742a83bb65')
            this.setState({ ofertas: lista.data })
// salva em localStorage os nvoos dados
            localStorage.setItem('super_avenida', JSON.stringify(lista.data))
// obtém a lista dos dados salvos em localstorage
        } else {
            let dados = await JSON.parse(localStorage.getItem('super_avenida'))
            this.setState({ ofertas: dados })
        }
    }


    render() {
        return (
            <div className='container mt-2'>
                <div className='card-deck'>
                    {this.state.ofertas.map(oferta => (
                        <ItemLista key={oferta.id}
                            id={oferta.id}
                            nome={oferta.nome}
                            marca={oferta.marca}
                            descricao={oferta.descricao}
                            preco={oferta.preco}
                            foto={oferta.foto} />
                    ))}

                </div>
            </div>
        )

    }
}
